.text {
  width: 50%;
  display: block;
  margin: 0 auto;
}

#project-details {
  padding-top: 40px;
  position: absolute;
}

article {
  /* padding: 0 0 0 800px; */
  width: 20%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-around;
 
}

p {
  text-align: center;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  background-color: #acb5c7;
  color: black;
  font-family: 'Rajdhani', sans-serif;
  font-weight: bold;
  font-size: 20px;
}

.img2 {
  padding-top: 75px;
}

.h1 {
  color: rgb(143, 57, 57);
  margin: auto;
}

@media only screen and (min-width: 768px) {
  * {
    /* tablet - two column display */
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 50px auto 30px;
    grid-template-areas: 
      "header header"
      "side content"
      "footer footer";
  }
}